'use strict';
const CONTAINER_CLASS = 'material-input-container';

class MaterialInput extends HTMLInputElement {
    // static get extends() {
    //     return 'input';
    // }

    // static get is() {
    //     return 'material-input';
    // }

    constructor() {
        super(); // always call super() first in the ctor. This also calls the extended class' ctor.
    }

    connectedCallback() {
        if (!this.parentNode.classList.contains(CONTAINER_CLASS)) {
            var container = document.createElement('div');
            container.className = CONTAINER_CLASS;
            this.parentNode.insertBefore(container, this);
            container.appendChild(this);
        }
    }
  // Use createdCallback instead of constructor to init an element.
  // createdCallback() {
  //   //   this.appendChild(mi);
  //     this.innerHTML = `
  //       <label for="">Label</label>
  //       <input type="text" name="text" />
  //     `;
  //   }

}
customElements.define('material-input', MaterialInput, {extends: 'input'});
